<template>
  <select v-model="selected" class="mb-3 form-control custom-select">
    <optgroup v-for="(year) in Object.keys(options).reverse()" :label="year">
      <option v-for="month in options[year]" :value="year + '/' + month.id">
        {{month.label}}
      </option>
    </optgroup>
  </select>
</template>

<script>
  import EventDate from '../../utils/EventDate';

  const MONTH_LABELS = {
    1: "Leden",
    2: "Únor",
    3: "Březen",
    4: "Duben",
    5: "Květen",
    6: "Červen",
    7: "Červenec",
    8: "Srpen",
    9: "Září",
    10: "Říjen",
    11: "Listopad",
    12: "Prosinec"
  };

  const defaultSelected = () => {
    const eventDate = new EventDate();
    return [eventDate.year, eventDate.month].join("/")
  };

  export default {
    props: {
      eventsIndex: {
        type: Array,
        required: true
      },
      onChange: {
        type: Function,
        required: false,
        default: function () {
        }
      }
    },
    data() {
      return {
        selected: defaultSelected()
      }
    },
    computed: {
      options() {
        return this.eventsIndex
          /*.sort((a, b) => {
            if ((a.year - b.year) === 0) {
              return a.month - b.month;
            } else {
              return b.year - a.year
            }
          })*/
          .reduce((result, event) => {
            if (!result[event.year]) {
              result[event.year] = []
            }

            result[event.year].push({
              id: event.month,
              label: MONTH_LABELS[event.month]
            });
            return result
          }, {})
      }
    },
    mounted() {
      this.onChange(this.selected)
    },
    watch: {
      selected(newValue) {
        this.onChange(newValue)
      }
    }
  }
</script>
