<template>
  <div>
    <div class="row">
      <events-date-select :events-index="EVENT_CALENDAR"
                          :on-change="selectDate"></events-date-select>
    </div>
    <div v-for="(list, date) in events" class="row animated fadeIn slow">
      <transition name="fade">
        <event-list v-if="selected === date" :date="date" :events="list">
        </event-list>
      </transition>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import { AsyncDataMixin } from 'vue-async-data-2';
  import EventList from '../../events/program/EventList';
  import EventsDateSelect from '../../events/EventsDateSelect';

  import ProgramBean from './ProgramBean'

  const programBean = new ProgramBean();

  export default {
    data() {
      return {
        selected: '',
        events: {}
      }
    },
    methods: {
      selectDate(date) {
        this.selected = date;
        programBean.fetch(date).then(events => {
          Vue.set(this.events, date, events);
        })
      }
    },
    mixins: [AsyncDataMixin],
    components: {
      EventsDateSelect,
      EventList
    }
  }
</script>

<style lang="scss">
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
    #program {
      transition: opacity 1s;
    }
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    #program {
      opacity: 0;
    }
  }
</style>
